@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;500&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;

}

body {
 
  font-family: 'Noto Sans JP', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

a {
  color: aqua;
}



.App{padding:20px;text-align:left}.won-card{background-color:#059b50}.lost-card{background-color:rgba(190,76,41,0.986)}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px;border-radius:15px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-details{text-align:right}.MatchDetailCard .additional-details>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px;border-radius:15px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:5rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:25px}.MatchPage .page-heading{margin:20px auto}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-row-gap:10px;grid-column-gap:10px}.HomePage .app-name{font-size:3rem;margin:30px auto}

.TeamTile{height:170px;text-align:center;border:1px solid #90e704;padding:20px;border-radius:20px}

